import React from "react";
import {
  Paper,
  Grid,
  makeStyles,
  Typography,
  List,
  ListItem,
  ListItemIcon,
  ListItemText
} from "@material-ui/core";
import "./App.css";
import PlainList from "./List";
import MaterialList from "./MaterialList";
import CheckIcon from "@material-ui/icons/Check";
import CodeSnippet from "./CodeSnippet";

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: "#f5f5f5",
    padding: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "left",
    color: theme.palette.text.primary
  },
  paperCenter: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.primary
  },
  inline: {
    display: "block"
  }
}));

function App() {
  const classes = useStyles();
  const b = text => <b>{text}</b>;
  const i = text => <i>{text}</i>;
  const bi = text => (
    <b>
      <i>{text}</i>
    </b>
  );
  return (
    <div className={classes.root}>
      <Grid container spacing={4}>
        <Grid container item xs={12} md={12} spacing={4}>
          <Grid item xs={12} md={12}>
            <Paper className={classes.paperCenter}>
              <Typography gutterBottom variant="h2" color="textPrimary">
                React Pagination
              </Typography>
              <Typography variant="h5" color="textPrimary">
                Implement load more on scroll using easy to use hooks! 🎉🎉
              </Typography>
            </Paper>
          </Grid>
          <Grid item xs={12} md={6}>
            <Paper className={classes.paper}>
              <Typography variant="h4" color="textPrimary">
                Features
              </Typography>
              <List>
                <ListItem key={0}>
                  <ListItemIcon>
                    <CheckIcon />
                    <ListItemText
                      primaryTypographyProps={{ color: "textPrimary" }}
                      primary="Fully Compatible with hooks"
                    ></ListItemText>
                  </ListItemIcon>
                </ListItem>
                <ListItem key={1}>
                  <ListItemIcon>
                    <CheckIcon />
                    <ListItemText
                      primaryTypographyProps={{ color: "textPrimary" }}
                      primary="Easy to use API"
                      secondary={
                        <Typography
                          component="span"
                          variant="body2"
                          color="textSecondary"
                          className={classes.inline}
                        >
                          Single line of code to initiate the hook
                        </Typography>
                      }
                    ></ListItemText>
                  </ListItemIcon>
                </ListItem>
                <ListItem key={2}>
                  <ListItemIcon>
                    <CheckIcon />

                    <ListItemText
                      primaryTypographyProps={{ color: "textPrimary" }}
                      primary="Customise load behaviour via parameters"
                      secondary={
                        <Typography
                          component="span"
                          variant="body2"
                          color="textSecondary"
                          className={classes.inline}
                        >
                          Specify fetch size, limit and scroller to monitor
                          fetch behaviour.
                        </Typography>
                      }
                    ></ListItemText>
                  </ListItemIcon>
                </ListItem>
                <ListItem key={3}>
                  <ListItemIcon>
                    <CheckIcon />

                    <ListItemText
                      primaryTypographyProps={{ color: "textPrimary" }}
                      primary="Configure custom loader, end of list behavior"
                      secondary={
                        <Typography
                          component="span"
                          variant="body2"
                          color="textSecondary"
                          className={classes.inline}
                        >
                          Add Custom loader to notify loading progress
                        </Typography>
                      }
                    ></ListItemText>
                  </ListItemIcon>
                </ListItem>
                <ListItem key={4}>
                  <ListItemIcon>
                    <CheckIcon />

                    <ListItemText
                      primaryTypographyProps={{ color: "textPrimary" }}
                      primary="Externalized fetch, You define how to fetch, hook decides when to fetch"
                      secondary={
                        <Typography
                          component="span"
                          variant="body2"
                          color="textSecondary"
                          className={classes.inline}
                        ></Typography>
                      }
                    ></ListItemText>
                  </ListItemIcon>
                </ListItem>
              </List>
            </Paper>
          </Grid>

          <Grid item xs={12} md={6}>
            <CodeSnippet
              title="Installation"
              text="npm install react-pagination"
            ></CodeSnippet>
            <CodeSnippet
              title="Import"
              text="import useLoadMoreOnScroll from 'react-pagination';"
            ></CodeSnippet>
          </Grid>
        </Grid>
        <Grid item xs={12} md={4}>
          <CodeSnippet
            title="Stub"
            text={`
  const {
         start,
         end,
         isFetching,
         doneFetching,
         setIsFetching,
         forceDonefetching
       } = useLoadMoreOnScroll({ fetchSize, scroller: ulElement, limit });
                      
  useEffect(()=>{
   // Notify the hook that you are about to fetch
   setIsFetching(true);
   //fetch data when start and end changes
   fetch(start, end).then(res=>{
    if(!res.body.hasMore){
      //Stop fetching, else the hook will keep 
      //update start and end to let you fetch more data till limit is exceeded
      forceDonefetching();
    }
    // Notify the hook that you are done fetching data
    setIsFetching(false);
    });
  },[start, end])
  `}
          ></CodeSnippet>
        </Grid>

        <Grid item xs={12} md={4}>
          <Paper className={classes.paper}>
            <Typography variant="h4" color="textPrimary">
              Vanilla HTML
            </Typography>
            <PlainList></PlainList>
          </Paper>
        </Grid>
        <Grid item xs={12} md={4}>
          <Paper className={classes.paper}>
            <Typography variant="h4" color="textPrimary">
              Material List
            </Typography>
            <MaterialList></MaterialList>
          </Paper>
        </Grid>
        <Grid item xs={12} md={12}>
          <Paper className={classes.paper}>
            <Typography variant="h4" color="textPrimary">
              Documentation
            </Typography>
            <Typography variant="h5" color="textPrimary">
              Behaviour
            </Typography>
            <Typography variant="p" color="textPrimary">
              The hook i.e.{bi(" useLoadMoreOnScroll ")}
              will let your component know when a new chunk of data {i(
                "needs"
              )}{" "}
              to be fetched. The need to fetch new data is derived from the
              below logic:
              <ul>
                <p>
                  In the {i("normal")} load more on scroll behaviour, data is
                  fetched propressively when the user scroll towards the bottom
                  of the list. As the minimum threshold is approached. The
                  minimum threshold is set to 25 pixels by default and can be
                  altered by setting the {i("minDelta")} prop. The hook updates
                  the start and end parameters as soon as the threshold is met
                  to notifiy the component to fetch next chunk of data.
                </p>
                <p>
                  {bi(" fetchSize ")} determines the chunk size and{" "}
                  {bi(" limit ")} determines the max number of chunks the hook
                  will request the component.
                  <br></br> {i("Example")}: if your fetch size is 10 and your
                  limit is set to 500, the hook will update the start and end
                  parameters with values like (0,10), (10,20)...(490, 500).
                </p>
                <p>
                  If a scroll is not available/visible on the {bi(" scroller ")}{" "}
                  element provided, the hook will continue updating the start
                  and end variables until the scroll is made available. After
                  that, the normal load more on scroll behaviour will be
                  restored.
                </p>
                <p>
                  The hook will keep on updating the {bi(" start ")} and{" "}
                  {bi(" end ")} variables until limit is breached or{" "}
                  {bi(" forceDonefetching ")} is called externally by the
                  component.
                </p>
              </ul>
            </Typography>
            <Typography variant="h5" color="textPrimary">
              API
            </Typography>
            <Typography color="textPrimary">
              <Typography variant="h6" color="textPrimary">
                Hook Input
              </Typography>
              <ul>
                <p>
                  {b("fetchSize ")} {i("{Number, 10}")} Determines the chunk
                  size during each fetch
                </p>
                <p>
                  {b("scroller ")} {i("{DOM Element}")} A react {i("Ref")}{" "}
                  representing the list scroll container. Progressive fetching will be done based on the scroll
                  position of this container.
                </p>
                <p>
                  {b("limit ")} {i("{Number, 500}")} Max. items to display in
                  the list
                </p>
                <p>
                  {b("mode ")} {i("{String, 'error'}")} Set to {i("debug")} to
                  see the hook logs
                </p>
                <p>
                  {b("minDelta ")} {i("{Number, 25}")} The threshold gap (in
                  pixels) between the container bottom and scroll position which
                  will trigger the next fetch
                </p>
              </ul>
              <Typography variant="h6" color="textPrimary">
                Hook Output
              </Typography>
              <ul>
                <p>
                  {b("start")} {i("{Number}")} Start index or offset for the new
                  chunk
                </p>
                <p>
                  {b("end")} {i("{Number}")} End index or max. items in each
                  chunk
                </p>
                <p>
                  {b("isFetching")} {i("{Boolean}")} Status to denote the fetch
                  status
                </p>
                <p>
                  {b("doneFetching")} {i("{Boolean}")} Either reached the{" "}
                  {i("limit")} or component has called {i("forceDonefetching")}
                </p>
                <p>
                  {b("setIsFetching")} {i("{Function}")} Externally set the
                  fetch status when fetching data.
                </p>
                <p>
                  {b("forceDonefetching")} {i("{Function}")} Terminate future
                  fetches
                </p>
              </ul>
            </Typography>
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
}

export default App;
